#home {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 60px 15px;
}

#homeImageOuterLayer {
  border: 20px solid rgba(0, 0, 0, 0.9);
  border-radius: 50%;
  width: 280px;
  height: 280px;
  margin: 30px 0;
}

#homeImageOuterLayer img {
  border: 20px solid rgba(0, 0, 0, 0.4);
  border-radius: 50%;
  width: 100%;
  height: 100%;
}

#homeInfo {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 40px;
}

#homeInfoHello {
  background-color: #a97afd;
  color: #fff;
  font-size: 14px;
  line-height: 24px;
  letter-spacing: 1px;
  font-weight: bold;
  padding: 10px 30px;
  border-radius: 20px 20px 20px 0px;
  margin-bottom: 20px;
}

#homeInfoName {
  font-size: 42px;
  font-weight: 600;
  line-height: 54px;
  color: #fff;
  margin-bottom: 10px;
}

#homeInfoRole {
  font-size: 20px;
  line-height: 24px;
  color: #fff;
  margin-bottom: 30px;
  font-weight: bold;
}

.homeInfoContactOption {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.homeInfoContactOption img {
  width: 14px;
  margin-right: 15px;
}

.homeInfoContactOption a {
  color: #fff;
}
